html {
  scroll-behavior: smooth;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.login {
  margin-top: 8%;
  padding: 40px;
  border-radius: 10px 10px 10px 10px;
  -moz-border-radius: 10px 10px 10px 10px;
  -webkit-border-radius: 10px 10px 10px 10px;
  border: 1px solid #ced4da;
  width: 520px;
  font-family: "Raleway", sans-serif;
}

.login h6 {
  margin-top: -10px;
  margin-left: auto;
  margin-right: auto;
  color: #ced4da;
}

.login h2 {
  margin-top: -10px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: -10px;
}

.login hr {
  margin-bottom: 10%;
}

.form-login .form-control {
  margin-bottom: 12px;
  padding: 8px 18px;
  width: 100%;
  border-radius: 10px 10px 10px 10px;
  -moz-border-radius: 10px 10px 10px 10px;
  -webkit-border-radius: 10px 10px 10px 10px;
}

.form-login button {
  margin-top: 50px;
  width: 100%;
  border-radius: 10px 10px 10px 10px;
  -moz-border-radius: 10px 10px 10px 10px;
  -webkit-border-radius: 10px 10px 10px 10px;
  letter-spacing: 2pt;
  font-size: 18px;
  font-weight: bolder;
  text-transform: uppercase;
  color: #e84118;
  border-radius: 10px 10px 10px 10px;
  -moz-border-radius: 10px 10px 10px 10px;
  -webkit-border-radius: 10px 10px 10px 10px;
  border: 3px solid #e84118;
}

.login .aLogin {
  font-size: 18px;
  font-weight: bolder;
  color: #e84118;
}

.login .aLogin:hover {
  transition: 0.3s;
  text-decoration: none;
  color: #b63211;
}

.login h4 {
  font-size: 12pt;
}

.login a {
  font-size: 18px;
  font-weight: bolder;
  color: #00a8ff;
}

.login a:hover {
  transition: 0.3s;
  text-decoration: none;
  color: #027dbb;
}

@media (min-width: 300px) and (max-width: 568px) {
  .login {
    margin-top: 25%;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
  }

  .login h4 {
    font-size: 8pt;
    margin-top: 10px !important;
    margin-left: 0px !important;
  }
  
  .login a {
    font-size: 12px;
  }
}

