.header-women {
    background-image: linear-gradient(grey, grey),
      url(/src/assets/images/ashim-d-silva-ZmgJiztRHXE-unsplash.jpg);
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: 50% 50%;
    background-blend-mode: multiply;
    margin-top: -15px;
    margin-left: auto;
    margin-right: auto;
    padding-top: 1%;
    padding-bottom: 0.3%;
    font-family: "Raleway", sans-serif;
  }
  
  .header-women .header h1 {
    color: #f5f6fa;
  }
  
  .product {
    width: 100%;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
  }
  
  .product .card-product {
    min-width: 300px;
    min-height: 400px;
    border: 1px solid #eee;
    overflow: hidden;
    padding: 10px;
    box-shadow: 2px 8px 20px #ddd;
    margin: 10px;
    transition: 0.5s linear;
  }
  
  .product .card-product:hover {
    box-shadow: none;
  }
  
  .product .card-product img {
    max-width: 300px;
    width: 100%;
    height: 100%;
    max-height: 300px;
    display: block;
    object-fit: cover;
  }
  
  .product .card-product h3 {
    text-transform: uppercase;
    margin: 10px 0;
  }
  
  .product .card-product h3 a {
    text-decoration: none;
    color: #333;
  }
  
  .product .card-product h3 a:hover {
    color: crimson;
  }
  
  .product .card-product span {
    color: crimson;
  }
  
  .product .card-product p {
    margin: 10px 0;
  }
  
  .product .card-product button {
    border: none;
    outline: none;
    background: #333;
    color: white;
    width: 100%;
    height: 40px;
    display: block;
    cursor: pointer;
    text-transform: uppercase;
    letter-spacing: 2px;
    margin: 15px 0;
  }
  